//引入路由
import VueRouter from 'vue-router'

//添加科目
import AddSubject from '../pages/subject/AddSubject.vue'
//添加试题
import Question from '../pages/question/Question.vue'
import AddNewQuestion from '../pages/question/AddNewQuestion.vue'
import ModifyQuestion from '../pages/question/ModifyQuestion'


export default new VueRouter({
    routes:[
        {path : '/',redirect:'/home'},
        {
            path : '/add_subject',
            component : AddSubject
        },
        {
            path : '/add_question',
            component : AddNewQuestion
        },
        {
            path : '/questions',
            component : Question
        },
        {
            path : '/modify',
            component : ModifyQuestion
        }
        
        // {
        //     path : '/about',
        //     component : About
        // }
    ]
})

