<template>
    <div class="add_new_question_panel">
        <div class="empty_block"></div>
        <!-- 题目区域 -->
        <div class="title white_btn">
            <div class="topic">科目：{{$route.query.title}}</div>
            <textarea v-model="title" placeholder="输入题目..."></textarea>
        </div>
        <!-- 切换开关 -->
        <div class="switch_btn white_btn">
            <div class="left" @click="switch_btn = true">选项</div>
            <div class="right" @click="switch_btn = false">解析</div>
            <div class="left focus" v-show="switch_btn">选项</div>
            <div class="right focus" v-show="!switch_btn">解析</div>
        </div>
        <!-- 选项区域 -->
        <div class="answer_area_btn white_btn" v-for="(a,index) in 4" :key="a.index" v-show="switch_btn">
            <!-- 正确选项 -->
            <div class="right_select icon-btn" v-show="right == index"></div>
            <div class="sign" v-if="index == 0" @click="right = index">A</div>
            <div class="sign" v-if="index == 1" @click="right = index">B</div>
            <div class="sign" v-if="index == 2" @click="right = index">C</div>
            <div class="sign" v-if="index == 3" @click="right = index">D</div>
            <input type="text" v-model="answer[index].content">
        </div>

        <!-- 解析区域 -->
        <div class="title white_btn" v-show="!switch_btn">
            <textarea v-model="analysis" style="top: 5vw; height: 40vw;" placeholder="添加一段解析..."></textarea>
        </div>

        <!-- 提交按钮 -->
        <div class="submit_btn white_btn" ref="submit_btn"
        @touchstart="$refs.submit_btn.classList.add('click')"
        @touchend="$refs.submit_btn.classList.remove('click')" @click="Submit">提 交</div>
    </div>
</template>

<script>
export default {
    name : 'AddNewQuestion',
    data(){
        return{
            title : '',
            switch_btn : true,
            right : 0,
            analysis : '',
            answer : [],
        }
    },
    methods:{
        Submit(){
            if(this.title.length == 0){
                alert('标题不能为空')
                return;
            }
            if(this.analysis.length < 5){
                alert('解析长度不能低于5个字符')
                return;
            }
            //选项不能为空
            for(let i=0;i<4;i++){
                if(this.answer[i].content == ''){
                    alert('选项内容不能为空')
                    return;
                }
            }
            //正式提交
            let date = new Date();
            let time_str = `${date.getFullYear()}/${date.getMonth()+1}/${date.getDate()}  ${date.getHours()}:${date.getMinutes()}`;
            let packet = {
                right_options : this.right,//正确选项
                belong_subject : this.$route.query.title,//所属科目
                submit_time : time_str,//提交时间
                title : this.title,//题目
                analysis: this.analysis,//题目解析
                answer : this.answer,//选项
                right_times : 0,//正确次数
                all_times : 0,//答题总次数
            }
            //如果存在这个科目，就在这个科目的数组后继续追加
            if(localStorage.getItem(`Note`+ `${this.$route.query.id}`)){
                let res = JSON.parse(localStorage.getItem(`Note`+ `${this.$route.query.id}`));
                res.push(packet);
                localStorage.setItem(`Note`+ `${this.$route.query.id}`,JSON.stringify(res));
            }
            else{
                let res = [];
                res.push(packet);
                localStorage.setItem(`Note`+ `${this.$route.query.id}`,JSON.stringify(res));
            }
            this.$router.back();
            
            
        }
    },
    beforeMount(){
        
        for(let i=0;i<4;i++){
            let empty = {
                content : '',
                selected_time : 0,
            }
            this.answer.push(empty);
        }
    }

}
</script>

<style lang="less">
@import url(../../assets/css/common.less);
.add_new_question_panel{
    position: absolute;
    left: 0; top: 0;
    width: 100vw;height: 100%;
    background: @bg;
    
    .title{
        position: relative;margin: 10vw 0 0 0;
        width: 100vw;height: 50vw;
        .topic{
            position: absolute;
            left: 5vw;top: 0vw;
            width: 80vw;
            height: 15vw;
            line-height: 15vw;
            text-align: left;
        }
        textarea{
            position: absolute;
            width: 90vw;height: 30vw;
            top: 15vw;left: 5vw;
            overflow: scroll;
        }
    }
    .switch_btn{
        position: relative;
        width: 40vw;height: 10vw;
        margin: 5vw 0 0 30vw;
        line-height: 10vw;
        border-radius: 4vw;
        .left,.right{
            position: absolute;left: 0;top: 0;
            width: 20vw;height: 100%;
            color: rgb(203, 203, 203);
        }
        .right{
            left: 20vw;
        }
        .left.focus,.right.focus{
            color: rgb(51, 51, 51);
        }
    }
    .answer_area_btn{
        position: relative;
        width: 100vw;height: 15vw;
        margin: 5vw 0 0 0;
        line-height: 15vw;
        .right_select{
            position: absolute;
            left: 0; top: 0;
            width: 5vw;height: 15vw;
            line-height: 15vw;
            color: rgb(23, 139, 8);
        }
        .sign{
            position: absolute;
            left: 0; top: 0;
            width: 15vw;height: 15vw;
            line-height: 15vw;
        }
        input{
            position: absolute;
            left: 15vw;
            top: 0;
            width: 85vw;height: 100%;
            outline: none;
        }
    }
    .submit_btn{
        position: relative;
        width: 100vw;height: 15vw;
        margin: 6vw 0 0 0;
        line-height: 15vw;
    }
}
</style>