<template>
    <div class="home_panel" >
        
        <!-- logo图标 -->
        <div class="logo" ref="logo">NoteX</div>



        <!-- 添加题目 -->
        <div class="white_btn new_subject icon-btn" ref="new_subject"
        @touchstart="$refs.new_subject.classList.add('click')" @touchend="$refs.new_subject.classList.remove('click')"
        @click="OpenSettingPanel(true)" v-show="!show.settings"></div>

        <!-- 已有题目 -->
        <div class="banner" ref="banner"> 

            <div class="add_subject white_btn" ref="add_subject" @touchstart="$refs.add_subject[index].classList.add('click')" @touchend="$refs.add_subject[index].classList.remove('click')" 
            v-for="(s,index) in $store.state.SUBJECT_ARR" :key="s.index" @click="QuestionPart(s)">{{s.title}}</div>

        </div>

        <!-- 遮罩面板 -->
        <div style="left:0;top: 0; width: 100vw;height: 100vh;" v-show="show.settings" @click="OpenSettingPanel(false)"></div>
        <div class="white_btn add_new_subject_btn" v-show="show.settings" ref="add_new_subject_btn"
        @touchstart="$refs.add_new_subject_btn.classList.add('click')" @touchend="$refs.add_new_subject_btn.classList.remove('click')"
        @click="AddNewSubject">新增科目</div>

        <router-view></router-view>
        <!-- 导航栏 -->
        <HeaderVue v-show="$route.path != '/' && $route.path != '/home'"/>
    </div>
</template>

<script>
import ShadowMask from '../tool/ShadowMask.vue'
import HeaderVue from '../tool/Header.vue';
export default {
    name : 'Main',
    components:{
        ShadowMask,
        HeaderVue,
    },
    data(){
        return{
            show:{
                settings: false,
            }
        }
    },
    methods:{
        //打开设置面板
        OpenSettingPanel(type){
            this.show.settings = type;
            if(type){
                this.$refs.logo.classList.add('blur');
                this.$refs.banner.classList.add('blur');
            }
            else{
                this.$refs.logo.classList.remove('blur');
                this.$refs.banner.classList.remove('blur');
            }
            console.log(1);
        },
        //新增科目
        AddNewSubject(){
            this.OpenSettingPanel(false);
            this.$router.push('/add_subject');
        },
        //刷题板块
        QuestionPart(value){
            this.$router.push(
                {
                    path  :'/questions',
                    query:{
                        id : value.id,
                        title : value.title,
                    }
                }
            );
        }
    }
}
</script>

<style lang="less">
@import url(../../assets/css/common.less);
.home_panel{
    position: absolute;left: 0; top: 0;
    width: 100vw;height: 100vh;
    background: @bg;
    
    .logo{
        position: absolute; left: 50%; top: 0;
        margin: 40vw 0 0 -18vw;
        width:36vw;height: 36vw;
        background: white;
        box-shadow: 8px 8px 12px #ebedf0;
        border-radius: 10vw;
        line-height: 36vw;
        color:@font ;
        font-size: 25px;
        
    }
    .new_subject{
        position: absolute;
        width: 16vw;height: 16vw;
        line-height: 16vw;
        right: 5vw;
        font-size: 5vw;
        top: 15vw;
        border-radius: 50%;
    }
    .add_new_subject_btn{
        position: absolute;
        width: 36vw;height: 13vw;
        line-height: 13vw;
        border-radius: 4vw;
        right: 5vw;
        top: 15vw;
    }
    .banner{
        position: absolute;
        width: 90vw;height: 76vw;
        left: 5vw;top:50vh;
        overflow: scroll;
        .add_subject{
            position: relative;
            width: 80vw;height: 14vw;
            margin: 1vw 0 5vw 5vw;
            border-radius: 4vw;
            line-height: 14vw;
        }
    }
    
}

</style>