<template>
    <div class="question_panel" ref="question_panel">

        <div class="empty_block"></div>

        <!-- 问题区 -->
        <div class="question_area white_btn" >
            <div class="topic">科目：{{$route.query.title}} ( 共{{results.length}}题 )</div>
            <div class="title">
                {{title}}
            </div>
            <div class="front icon-btn" v-show="point > 0" @click="LastQuestion"></div>
            <div class="rear icon-btn" v-show="point < results.length-1" @click="NextQuestion"></div>
            <!-- 修改按钮 -->
            <div class="modify_btn" @click="ModifyItem">修改</div>
        </div>
        <div class="answer_btn white_btn full1" ref="answer_btn"
        @touchstart="index == results[point].right_options ?$refs.answer_btn[index].classList.add('right'):$refs.answer_btn[index].classList.add('false')"
        @touchend="index == results[point].right_options ?$refs.answer_btn[index].classList.remove('right'):$refs.answer_btn[index].classList.remove('false')" 
        v-for="(a,index) in answer" :key="a.index"
        @click="SelectOptions(index)">{{a.content}}</div>

        <div class="add_new_question" ref="add_new_question"
        @touchstart="$refs.add_new_question.classList.add('click')"
        @touchend="$refs.add_new_question.classList.remove('click')" @click="AddNewRecord">添加新记录</div>
        <!-- 查看解析 -->
        <div class="check_analysis" ref="check_analysis"
        @touchstart="$refs.check_analysis.classList.add('click')"
        @touchend="$refs.check_analysis.classList.remove('click')"
        @click="show_analysis = true">解析</div>

        <div class="delete_btn" ref="delete_btn"
        @touchstart="$refs.delete_btn.classList.add('click')"
        @touchend="$refs.delete_btn.classList.remove('click')"
        @click="DeleteItem">删除</div>
    
        <!-- 解析面板 -->
        <div class="analysis_panel" v-show="show_analysis" @click="show_analysis = false;">
            <div class="topic icon-btn">解 析 </div>
            <div class="container">{{results[point].analysis}}</div>
        </div>


        <!-- 点赞 -->
        <div class="good icon-btn" v-for="g in good_arr" :key="g.index"></div>
    
    </div>
</template>

<script>
import { Dialog } from 'vant';
export default {
    name : 'Question',
    data(){
        return{
            results :[],
            point : 0, 
            title : '',
            answer : [],
            show_analysis : false,
            good_arr : [],
        }
    },
    methods:{
        //修真按钮的文字尺寸
        ChangeBtnTextSize(){
            for(let i=0;i<4;i++){
                this.$refs.answer_btn[i].classList.remove('middle');
                this.$refs.answer_btn[i].classList.remove('longer');
            }
            for(let i=0;i<4;i++){
                if(this.answer[i].content.length > 18 && this.answer[i].content.length < 48){
                    this.$refs.answer_btn[i].classList.add('middle');
                }
                else if(this.answer[i].content.length > 48){
                    this.$refs.answer_btn[i].classList.add('longer');
                }
            }

        },
        //添加新记录
        AddNewRecord(){
            this.$router.push({
                path : '/add_question',
                query : this.$route.query,
            })
        },
        //上一个问题
        LastQuestion(){
            
            this.point -= 1;
            this.answer = this.results[this.point].answer;
            this.title = this.results[this.point].title;
            this.ChangeBtnTextSize();
            
        },
        NextQuestion(){
            
            this.point += 1;
            this.answer = this.results[this.point].answer;
            this.title = this.results[this.point].title
            this.ChangeBtnTextSize();
            
        },
        //选择
        SelectOptions(value){
            if(value == this.results[this.point].right_options){
                if(this.good_arr.length>0){
                    this.good_arr = [];
                }
                setTimeout(()=>{
                    this.good_arr.shift();
                },290)
                this.good_arr.push({});
                setTimeout(() => {
                    if(this.point < this.results.length-1){
                        let before = this.point;
                        this.point += Math.floor(Math.random()*this.results.length);
                        this.point = this.point%this.results.length;
                        if(before == this.point){
                            this.point += 1;
                            this.point = this.point%this.results.length;
                        }
                        
                        this.answer = this.results[this.point].answer;
                        this.title = this.results[this.point].title;
                        this.ChangeBtnTextSize();
                    }
                    else if(this.point == this.results.length-1){
                        this.point = 0;
                        this.answer = this.results[this.point].answer;
                        this.title = this.results[this.point].title;
                        this.ChangeBtnTextSize();
                    }
                    
                }, 100);
                
            }
            else{
                this.show_analysis = true;
            }
        },
        //删除
        DeleteItem(){
            if(confirm("确定删除吗？")){
                this.results.splice(this.point,1);
                localStorage.setItem(`Note`+ `${this.$route.query.id}`,JSON.stringify(this.results));
                let rand = Math.floor(Math.random()*this.results.length);
                this.point = rand;
                this.answer = this.results[rand].answer;
                this.title = this.results[rand].title;
            }else{
                return;
            }
        },
        //修改该题目
        ModifyItem(){
            if(confirm("确定修改吗？")){
                this.$router.push({
                    path : '/modify',
                    query : {
                        info : this.$route.query,
                        item : this.results[this.point],
                        index : this.point,
                    }
                })
            }else{
                return;
            }
        }
        
    },  
    beforeMount(){
        if(localStorage.getItem(`Note`+ `${this.$route.query.id}`)){
            let res = JSON.parse(localStorage.getItem(`Note`+ `${this.$route.query.id}`));
            this.results = res;
            if(res.length>0){
                let rand = Math.floor(Math.random()*res.length);
                this.point = rand;
                this.answer = res[rand].answer;
                this.title = res[rand].title;
            }
        }
        if(this.results.length == 0){
            let empty={
                content : '',
            }
            this.answer.push(empty);
            this.results = [
                {
                    right_options : "",//正确选项
                    belong_subject : "",//所属科目
                    submit_time : "",//提交时间
                    title : "空空如也",//题目
                    analysis: "",//题目解析
                    answer : "",//选项
                    right_times : 0,//正确次数
                    all_times : 0,//答题总次数
                }
            ]
            this.title = "这里空空如也"
        }
        
    },
    mounted(){
        // for(let i=0;i<this.answer.length;i++){
        //     if(this.answer[i].content.length > 18 && this.answer[i].content.length < 48){
        //         this.$refs.answer_btn[i].classList.add('middle');
        //     }
        //     else if(this.answer[i].content.length > 48){
        //         this.$refs.answer_btn[i].classList.add('longer');
        //     }
        // }
        this.ChangeBtnTextSize();
        
    }
}
</script>

<style lang="less">
@import url(../../assets/css/common.less);
.question_panel{
    position: absolute;
    left: 0; top: 0;
    width: 100vw;height: 100%;
    background: @bg;
    .question_area{
        position: relative;margin: 10vw 0 0 0;
        width: 100vw;height: 60vw;
        .topic{
            position: absolute;
            left: 5vw;top: 0vw;
            width: 80vw;
            height: 15vw;
            line-height: 15vw;
            text-align: left;
        }
        .title{
            position: absolute;
            width: 80vw;height: 30vw;
            top: 15vw;left: 10vw;
            overflow-y: scroll;
            overflow-x: hidden;
            textarea:focus{
                outline: none;
            }
        }
        .modify_btn{
            position: absolute;
            right: 0vw; top: 0;
            width: 20vw;height: 10vw;
            line-height: 15vw;
            color: #648d9e;
        }
        .front,.rear{
            position: absolute;
            left: 0; top: 30vw;
            margin: -5vw 0 0 0;
            width: 10vw;
            font-size: 8vw;
            line-height: 10vw;
            height: 10vw;
            
        }
        .rear{
            left: 90vw;
        }
    }
    .answer_btn{
        width: 80vw;height: 14vw;
        margin: 6vw 0 0 10vw;
        line-height: 10vw;
        border-radius: 4vw;
        padding: 2vw;
        box-sizing: border-box;
        overflow: hidden;
    }
    .answer_btn.middle{
        font-size: 3vw;
        line-height: 5vw;
    }
    .answer_btn.longer{
        height: 22vw;
        font-size: 2vw;
        line-height: 4vw;
    }
    .answer_btn.right{
        // font-size: 3vw;
        // line-height: 3vw;
        color: white;
        background: rgb(22, 168, 12);
    }
    .answer_btn.false{
        // font-size: 3vw;
        // line-height: 3vw;
        color: white;
        background: rgb(128, 7, 7);
    }
    .add_new_question,.check_analysis,.delete_btn{
        position: fixed;
        bottom: 7vw;left: 30vw;
        width: 40vw;height: 10vw;
        line-height: 10vw;
        border-radius: 3vw;
        color: white;
        background: rgb(212, 212, 212);
    }
    .delete_btn{
        left: 5vw;
        width: 18vw;
    }
    .check_analysis{
        left: 75vw;
        width: 20vw;
    }
    .check_analysis.click,.add_new_question.click,.delete_btn.click{
        background: #1e333c;
    }
    .analysis_panel{
        position: fixed;
        left: 0; top: 0;
        width: 100vw;height: 100vh;
        background: rgba(255, 255, 255, 0.9);
        z-index: 100;
        .topic{
            position: absolute;
            left: 8vw;top: 20vw;line-height: 10vw;
            width: 50vw;
            height: 10vw;
            font-size: 6vw;
            text-align: left;
            color: rgb(124, 124, 124);
            margin: -5vw 0 0 0;
        }
        .container{
            position: absolute;
            width: 80vw; left: 0;top: 50%;
            // height: 120vw;
            margin: 0 0 0 10vw;
            transform: translate(0,-50%);

        }
    }
    .good{
        position: absolute; 
        left: 50%;top: 50%;
        width: 10vw;height: 10vw;
        margin: -35vw 0 0 -5vw;
        font-size: 10vw;
        color: green;
        animation: good_animate .3s ease-out 1;

    }
    @keyframes good_animate {
       0%{
          transform: scale(0);
          opacity: 0;
       }
       40%{
          transform: scale(1);
          opacity: 1;
       }
       100%{
          transform: scale(2);
          opacity: 0;
       }
    }
}
</style>