<template>
    <div class="add_subject_panel">
        <!-- 新建科目 -->
        <div class="title">新增科目</div>
        <div class="new_subject white_btn">
            <input type="text" v-model="text" placeholder="请输入科目名称">
        </div>
        <div class="title"></div>

        <div class="submit_btn white_btn" ref="submit_btn"
        @touchstart="$refs.submit_btn.classList.add('click')" @touchend="$refs.submit_btn.classList.remove('click')"
        @click="Submit()">提 交</div>
        <!-- 输入任意文本 -->
        <!-- <van-field v-model="text" label="文本" /> -->
        
    </div>
</template>

<script>


export default {
    name : 'AddSubject',
    
    data(){
        return {
            text : '',
        }
    },
    methods:{
        //提交科目
        Submit(){
            if(this.text.length >0 && this.text.length < 15){
                //正则过滤特殊字符和空格
                const pattern=/[`~!@#$^\-&*()=|{}':;',\\\[\]\.<>\/?~！@#￥……&*（）——|{}【】'；：""'。，、？\s]/g;
                this.text = this.text.replace(pattern,"");
                let date = new Date();
                let time_str = `${date.getFullYear()}/${date.getMonth()+1}/${date.getDate()}  ${date.getHours()}:${date.getMinutes()}`;
                let item = {
                    id : new Date().getTime(),//用时间戳作为id
                    title : this.text,
                    time : time_str,
                }
                this.$store.state.SUBJECT_ARR.push(item);
                console.log(this.$store.state.SUBJECT_ARR)
                localStorage.setItem('NoteX_Subject',JSON.stringify(this.$store.state.SUBJECT_ARR));
                this.text = "";
                this.$router.back();
            }
            else{
                alert('科目名称长度在1-15个字符之间');
            }
        },
        
    }
}
</script>

<style lang="less">
@import url(../../assets/css/common.less);
.add_subject_panel{
    position: absolute;
    left: 0; top: 20vw;
    width: 100vw;height: 100%;
    background: @bg;
    .title{
        position: relative;
        width: 100vw;
        line-height: 20vw;
        height: 20vw;
        margin: 0;
    }
    .new_subject{
        position: relative;
        width: 100vw;height: 16vw;
        margin: 0;
        left: 0;top: 0;
        border-radius: 0%;
        background: rgb(255, 255, 255);
        input{
            width: 90%;height: 100%;
            left: 5vw;
            
        }
        input:focus{
            outline: black;
        }
    }
    .submit_btn{
        width: 100vw;height: 16vw;
        line-height: 16vw;
    }
}
</style>